import React, { useEffect, useState, useCallback, useRef } from "react";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";
import { useNavigate } from "react-router-dom";

const DashboardStatement = () => {
  const [showModal, setShowModal] = useState(false);
  const [showStatusModal, setShowStatusModal] = useState(false);
  const [bookingStatement, setBookingStatement] = useState(null);
  const [selectedBooking, setSelectedBooking] = useState(null);
  const [newStatus, setNewStatus] = useState("");
  const [centreBookings, setCentreBookings] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  //status for booking
  const [bookingStatus, setBookingStatus] = useState("");
  const navigate = useNavigate();

  // Fetch centre bookings

    const fetchCentreBookings = async () => {
      try {
        setLoading(true);
        const centreId = localStorage.getItem("centreId");

        if (!centreId) {
          throw new Error("No centreId found in localStorage");
        }

        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/bookings/centre/${centreId}`
        );

        setCentreBookings(response.data.bookings || []);
        setCurrentPage(1); // Reset to first page when data loads
        setError(null);
      } catch (err) {
        console.error("Error fetching bookings:", err);
        setError("Failed to load bookings. Please try again later.");
      } finally {
        setLoading(false);
      }
    };
  useEffect(() => {
    fetchCentreBookings();
  }, []);

  // Debug currentPage changes
  useEffect(() => {
  }, [currentPage]);

  const canEnableStatus = (item) => {
  if (item.status.toLowerCase() !== "pending") return false;
  if (!item.appointmentDate || !item.appointmentTime) return false;

  // Example: "2025-09-30" and "11:10 am - 11:20 am"
  const datePart = item.appointmentDate;

  // take the start time only ("11:10 am")
  const startTime = item.appointmentTime.split("-")[0].trim();

  // Convert "11:10 am" into 24-hour format
  const timeMatch = startTime.match(/(\d+):(\d+)\s*(am|pm)/i);
  if (!timeMatch) return false;

  let [_, hh, mm, period] = timeMatch;
  hh = parseInt(hh, 10);
  mm = parseInt(mm, 10);

  if (period.toLowerCase() === "pm" && hh !== 12) hh += 12;
  if (period.toLowerCase() === "am" && hh === 12) hh = 0;

  // Build full datetime string
  const appointmentDateTime = new Date(`${datePart}T${String(hh).padStart(2, "0")}:${String(mm).padStart(2, "0")}:00`);

  const now = new Date();
  return now >= appointmentDateTime;
};


const handleCheckBeforeStatus = async (bookingId) => {
  try {
    // â Fetch from backend using serviceBookingId
    console.log("Calling");
    
    const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/reports/check/${bookingId}`, {
      method: "GET",
    });

    const data = await response.json();

    // if (!response.ok) {
    //   toast.error(data.message || "Something went wrong!");
    //   return false;
    // }

    // â Check if files or notes exist
    const hasFilesOrNotes = (data.files && data.files.length > 0) || data.notes;
    
    if (hasFilesOrNotes) {
      return true;
    } else {
      return false;
    }
  } catch (error) {
    console.error("Error checking report:", error);
    toast.error("Failed to check report status.");
    return false;
  }
};

const shouldDisableCancelButton = (appointmentDate, appointmentTime) => {
  console.log("calling disable button");
  console.log("date",appointmentDate);
  console.log("time",appointmentTime);
  
  
  
  
  // FIXED: Check if date OR time is missing
  if (!appointmentDate || !appointmentTime) return true;

  const datePart = appointmentDate;
  const startTime = appointmentTime.split("-")[0].trim();
  console.log("Date",datePart);
  
  console.log("Start time",startTime);
  

const timeMatch = startTime.match(/(\d+):(\d+)\s*([ap]\.?m\.?)/i);
  if (!timeMatch) return true;

  let [_, hh, mm, period] = timeMatch;
  hh = parseInt(hh, 10);
  mm = parseInt(mm, 10);

  if (period.toLowerCase() === "pm" && hh !== 12) hh += 12;
  if (period.toLowerCase() === "am" && hh === 12) hh = 0;

  const appointmentDateTime = new Date(`${datePart}T${String(hh).padStart(2, "0")}:${String(mm).padStart(2, "0")}:00`);
  const now = new Date();
  
  const timeDifferenceHours = (appointmentDateTime.getTime() - now.getTime()) / (1000 * 60 * 60);
  
  console.log(`Time difference: ${timeDifferenceHours} hours`);
  
  // Return true to disable if within 2 hours or in the past
  return timeDifferenceHours <= 2;
};

  // Update booking status
  const updateStatus = useCallback(async (id, updatedStatus, appointmentDate, appointmentTime) => {
  try {

    //complete unabling only after appointment start time

     const statusLower = updatedStatus.toLowerCase();

    if (statusLower === "completed") {      
      const validationItem = {
        status: "pending",
        appointmentDate,
        appointmentTime
      };
      
      const canEnable = canEnableStatus(validationItem);
      
      if (!canEnable) {
        toast.error("You can only change the status after the appointment time");
        setShowStatusModal(false);
        return;
      }
         
      const hasDocuments = await handleCheckBeforeStatus(id);
      console.log("Document check result:", hasDocuments);
      
      if (!hasDocuments) {
        toast.error("Please upload at least one report or note before changing the status to completed");
        setShowStatusModal(false);
        return;
      }


    }

      else if (statusLower=== "cancelled") {
      // Check 1: Cannot cancel within 2 hours of appointment time
      const canCancel = shouldDisableCancelButton(appointmentDate, appointmentTime,statusLower);
      console.log("cancel ans",canCancel);
      
      
      if (canCancel) {
        toast.error("Cancellation must be done 2+ hours before the appointment time");
        setShowStatusModal(false);
        return;
      }


      const hasDocuments = await handleCheckBeforeStatus(id);
      console.log("Document check result for cancel:", hasDocuments);
      
      if (hasDocuments) {
        toast.error("Cannot cancel booking as documents have already been uploaded");
        setShowStatusModal(false);
        return;
      }
    }
    


    console.log("Making API call to update status...");
    await axios.patch(
      `${process.env.REACT_APP_BACKEND_URL}/api/bookings/${id}/status`,
      { status: updatedStatus }
    );

    // Update local state
    setCentreBookings((prev) =>
      prev.map((booking) => ({
        ...booking,
        services: booking.services.map((service) =>
          service._id === id ? { ...service, status: updatedStatus } : service
        ),
      }))
    );
    
    toast.success("Status Updated Successfully");
    setShowStatusModal(false);
    setNewStatus("");
    setTimeout(()=>{
      fetchCentreBookings();
    },3000);
    
  } catch (err) {
    console.error("Error updating status:", err);
    console.error("Error details:", err.response?.data || err.message);
    toast.error("Failed to update status");
  }
}, []);

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const Id = localStorage.getItem("centreId");
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/bookings/searchBooking/Admin`,
          {
            params: { status: bookingStatus, centreId: Id }, // send both filters
          }
        );

        setCentreBookings(response.data.bookings || []);
        setCurrentPage(1); // Reset to first page when data changes
      } catch (err) {
        console.error("Error fetching bookings:", err);
      }
    };

    fetchBookings();
  }, [bookingStatus]);

  // Flatten the nested booking structure for pagination
  const flattenedBookings = centreBookings.flatMap((booking) =>
    booking.services.map((item) => ({ booking, item }))
  );

  // Pagination calculations
  const packagesPerPage = 10; // Changed to 10 records per page
  const indexOfLastPackage = currentPage * packagesPerPage;
  const indexOfFirstPackage = indexOfLastPackage - packagesPerPage;
  const currentItems = flattenedBookings.slice(
    indexOfFirstPackage,
    indexOfLastPackage
  );
  const totalPages = Math.ceil(flattenedBookings.length / packagesPerPage);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

     const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};

  const renderPagination = () => {
    const pages = [];
    const maxVisible = 4;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalPages) {
      endPage = totalPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

     pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
        >
          Prev
        </button>
      </li>
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button className="page-link" onClick={() => handlePageChangeWithScroll(i)}>
            {i}
          </button>
        </li>
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalPages && handlePageChangeWithScroll(currentPage + 1)
          }
        >
          Next
        </button>
      </li>
    );

        pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalPages)}
        >
          Last
        </button>
      </li>
    );

    return pages;
  };

    const modalRef = useRef(null);
      
          useEffect(() => {
            // Handle click outside
            const handleClickOutside = (event) => {
              if (
                modalRef.current &&
                !modalRef.current.contains(event.target) &&
                showModal
              ) {
                showModal(false);
              }
            };
        
            document.addEventListener("mousedown", handleClickOutside);
        
            // ð Scroll lock logic
            if (showModal) {
              document.body.style.overflow = "hidden"; // Disable background scroll
            } else {
              document.body.style.overflow = "auto"; // Enable background scroll
            }
        
            return () => {
              document.removeEventListener("mousedown", handleClickOutside);
              document.body.style.overflow = "auto"; // Cleanup when component unmounts
            };
          }, [showModal]);

    
        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
              showStatusModal
            ) {
              showStatusModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // ð Scroll lock logic
          if (showStatusModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [showStatusModal]);

  // Get status badge class
  const getStatusBadgeClass = (status) => {
    const statusLower = status.toLowerCase();
    if (statusLower === "confirmed" || statusLower === "completed")
      return "bg-success";
    if (statusLower === "cancelled") return "bg-danger";
    if (statusLower === "pending") return "bg-warning";
    return "bg-secondary";
  };

  // Handle status modal open
  const handleStatusModalOpen = (item) => {
    console.log("Item",item);
    
    setSelectedBooking(item);
    setNewStatus(item.status);
    setShowStatusModal(true);
  };



  // Handle view modal open
  const handleViewModalOpen = (booking, item) => {
    setBookingStatement({ booking, item });
    setShowModal(true);
  };

  if (loading) {
    return (
      <div className="dashboard-body__content">
        <div className="text-center py-5">Loading bookings...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="dashboard-body__content">
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
      </div>
    );
  }


  return (
    <div className="dashboard-body__content">
      {/* Statement section start */}
      <div className="row gy-4">
        <div className="col-12">
          <div className="card common-card border border-gray-five">
            <div className="card-body">
              <div 
                style={{
                  display: "flex",
                  justifyContent: "space-between", // evenly space heading and dropdown
                  alignItems: "center", // vertical alignment
                  gap: "20px", // small consistent gap
                  flexWrap:"wrap",
                }}
                className="booking-smallscreen"
              >
                <h4 className="booking-heading">Booking Statement</h4>
                <select
                  id="time"
                  className="common-input border-gray-five w-sm-100  w-25 booking-selecting"
                  defaultValue="all"
                  value={bookingStatus}
                  onChange={(e) => setBookingStatus(e.target.value)}
                >
                  <option value="">Select Status</option>
                  <option value="Cancelled">Cancelled</option>
                  <option value="Completed">Completed</option>
                  <option value="pending">Upcoming</option>
                </select>
              </div>
              {flattenedBookings.length === 0 ? (
                <div className="text-center py-5">
                  No bookings found for your centre.
                </div>
              ) : (
                <>
                  <div className="table-responsive">
                    <table className="table text-body mt--24">
                      <thead>
                        <tr>
                          <th>Booking Date</th>
                          <th>Booking ID</th>
                          <th>Appointment Date</th>
                          <th>Time slots</th>
                          <th>Patient Name</th>
                          <th>Patient Email</th>
                          <th>Patient Mobile</th>
                          <th>Service Type</th>
                          <th>Service Name</th>
                          <th>Price</th>
                          <th>Status</th>
                          <th className="text-center">Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {currentItems.map(({ booking, item }, index) => (
                          


                          <tr key={`${booking._id}-${item._id}-${index}`}>
                            <td>
                              {new Date(booking?.bookingDate).toLocaleString()}
                            </td>
                            <td>{item.bookingId}
                              
                            </td>
                            <td data-label="Date">{item.appointmentDate}</td>
                            <td data-label="Time">{item.appointmentTime}</td>
                            <td
                              data-label="Patient Name"
                              onClick={() => {
                                navigate(
                                  `/user-medical-history/${booking?.userDetails?._id}`
                                );
                              }}
                              style={{
                                color: "blue",
                                textDecoration: "underline",
                                cursor: "pointer",
                              }}
                            >
                              {booking.userDetails.firstName}{" "}
                              {booking.userDetails.lastName}
                            </td>
                            <td data-label="Patient Email">
                              {booking.userDetails.email}
                            </td>
                            <td data-label="Patient Mobile">
                              {booking.userDetails.mobileNumber}
                            </td>
                            <td data-label="Service Type">{item.type}</td>
                            <td data-label="Service Name">{item.name}</td>
                            <td data-label="Price">â¹{item.totalPrice}</td>
                            <td data-label="Status">
                              <span
                                className={`badge ${getStatusBadgeClass(
                                  item.status
                                )}`}
                              >
                                {item.status==="pending" ?"Upcoming":item.status}
                              </span>
                              
                            </td>

                            <td
                              data-label="Action"
                              style={{ display: "flex", gap: "10px" }}
                            >
                              <button
                                type="button"
                                className="btn btn-sm btn-success"
                                
 
   style={{
      backgroundColor:
        item.status === "Cancelled" || item.status === "Completed"
          ? "#6c757d"
          : undefined,
      borderColor:
        item.status === "Cancelled" || item.status === "Completed"
          ? "#6c757d"
          : undefined,
      cursor:
        item.status === "Cancelled" || item.status === "Completed"
          ? "not-allowed"
          : "pointer",
      opacity:
        item.status === "Cancelled" || item.status === "Completed" ? 0.65 : 1,
    }}
     onClick={() => {
      if (item.status !== "Cancelled" && item.status !== "Completed") {
        setSelectedBooking(item);
        setNewStatus(item.status); 
        setShowStatusModal(true);
      }
    }}
                              >
                                Status
                              </button>

                              <button
                                type="button"
                                className="btn btn-sm btn-info"
                                onClick={() =>
                                  handleViewModalOpen(booking, item)
                                }
                              >
                                View
                              </button>
                              <button
                                type="button"
                                className="btn btn-sm btn-primary"
                                  style={{
    backgroundColor: item.status === "Cancelled" ? "#6c757d" : undefined,
    borderColor: item.status === "Cancelled" ? "#6c757d" : undefined,
    cursor: item.status === "Cancelled" ? "not-allowed" : "pointer",
    opacity: item.status === "Cancelled" ? 0.65 : 1,
  }}
                                disabled = {item.status === "Cancelled"}
                                // disabled={item.status !== "pending"}
                                onClick={() => {
                                  navigate("/centre/upload-report", {
                                    state: {
                                      service: item, // service/package details
                                      booking: booking, // full booking details if needed
                                      userId: booking?.userDetails?._id,
                                       bookingId: item.bookingId
                                    },
                                  });
                                }}
                              >
                                Report
                              </button>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>

                  {/* Pagination Controls */}
                  <div className="flx-between gap-2">
                    <div className="paginate-content flx-align flex-nowrap gap-3">
                      <span className="paginate-content__text fs-14">
                        Showing {indexOfFirstPackage + 1} â{" "}
                        {Math.min(indexOfLastPackage, flattenedBookings.length)}{" "}
                        of {flattenedBookings.length}
                      </span>
                    </div>
                    <nav aria-label="Doctor pagination" className="mt-3">
                      <ul className="pagination justify-content-center">
                        {renderPagination()}
                      </ul>
                    </nav>
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      </div>

      {showModal && bookingStatement && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
            padding:"15px",
            overflowY:"auto",
          }}
          onClick={() => setShowModal(false)}
        >
          <div
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "700px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "relative",
              overflow: "hidden",
              maxHeight:"90vh",
              overflowY:"auto",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                position:"sticky",
                top:0,
                zIndex:10,
              }}
            >
              Booking Details
              <button
                onClick={() => setShowModal(false)}
                style={{
                  position: "absolute",
                  top: "10px",
                  right: "10px",
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                  overflowY:"auto",

                }}
              >
                &times;
              </button>
            </div>

            <div
              style={{
                display: "flex",
                padding: "20px",
                flexWrap: "wrap",
                gap: "20px",
              }}
            >
              {/* Left Column */}
              <div style={{ flex: "1", minWidth: "250px" }}>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Booking ID</strong>
                  </p>
                  <p>{bookingStatement.item?.bookingId}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Appointment Date</strong>
                  </p>
                  <p>{bookingStatement.item?.appointmentDate}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Appointment Time</strong>
                  </p>
                  <p>{bookingStatement.item?.appointmentTime}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Patient Name</strong>
                  </p>
                  <p>
                    {bookingStatement.booking?.userDetails?.firstName}{" "}
                    {bookingStatement.booking?.userDetails?.lastName}
                  </p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Patient Email</strong>
                  </p>
                  <p>{bookingStatement.booking?.userDetails?.email}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Patient Mobile</strong>
                  </p>
                  <p>{bookingStatement.booking?.userDetails?.mobileNumber}</p>
                </div>
              </div>

              {/* Right Column */}
              <div style={{ flex: "1", minWidth: "250px" }}>
                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Booking Type</strong>
                  </p>
                  <p>{bookingStatement.item?.type}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>
                      {bookingStatement.item?.type === "package"
                        ? "Package Name"
                        : "Service Name"}
                    </strong>
                  </p>
                  <p>{bookingStatement.item?.name}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  {bookingStatement.item?.type === "package" ? (
                    <>
                      <p>
                        <strong style={{ color: "black" }}>
                          Services Name
                        </strong>
                      </p>
                      {bookingStatement.item?.includedServices?.map(
                        (service, key) => (
                          <p key={key}>{service}</p>
                        )
                      )}
                    </>
                  ) : (
                    <>
                      <p>
                        <strong style={{ color: "black" }}>
                          Category Name
                        </strong>
                      </p>
                      <p>{bookingStatement.item?.category}</p>
                    </>
                  )}
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Doctor Name</strong>
                  </p>
                  {bookingStatement.item?.doctors?.map((doc, index) => (
                    <p key={index}>{doc}</p>
                  ))}
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Price</strong>
                  </p>
                  <p>â¹{bookingStatement.item?.totalPrice}</p>
                </div>

                <div style={{ marginBottom: "20px" }}>
                  <p>
                    <strong style={{ color: "black" }}>Status</strong>
                  </p>
                  <p>{bookingStatement.item?.status==="pending"?"Upcoming":bookingStatement.item?.status}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Status Update Modal */}
      {showStatusModal && selectedBooking && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
          onClick={() => setShowStatusModal(false)}
        >
          <div
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "500px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "relative",
              overflow: "hidden",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
              }}
            >
              Update Status
              <button
                onClick={() => setShowStatusModal(false)}
                style={{
                  position: "absolute",
                  top: "10px",
                  right: "10px",
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            <div style={{ padding: "20px" }}>
              <div className="mb-3">
                <label className="form-label fw-bold">Status</label>
                <select
                  className="form-select"
                  value={newStatus}
                  onChange={(e) => setNewStatus(e.target.value)}
                >
                  <option value="">-- Select Status --</option>
                  <option value="Cancelled">Cancelled</option>
                  <option value="Completed">Completed</option>
                </select>
              </div>
              <div className="d-flex justify-content-end">
                <button
                  className="btn btn-primary"
                  onClick={() =>
                    updateStatus(selectedBooking.bookingId,newStatus,selectedBooking.appointmentDate,selectedBooking.appointmentTime)
                  }
                  disabled={!newStatus}
                >
                  Update
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

 <style>
  {`
  /* Default layout: horizontal */


  /* Small screens (â¤425px) */
  @media (max-width: 426px) {
    .booking-smallscreen {
      flex-direction: column;
      align-items: flex-start;
      gap: 0px !important;
    }

    .booking-heading {
      width: 100%;
      text-align: left;
    }
  }
     @media (max-width: 495px){
    .booking-selecting {
      width: 100% !important;
    }
  }
  }
  `}
</style>

      <ToastContainer position="top-right" autoClose={3000} />

      {/* Statement section End */}
    </div>
    
  );
};

export default DashboardStatement;
